.nav-background{
    background-color: black;
}
.nav{
    position: fixed;
    top: 0;
    padding: 20px;
    width: 100%;
    height: 30px;
    z-index: 1;

    /*animations*/
transition-timing-function: ease-in;
transition: all 0.5s;
}


.nav-content{
    display: flex;
    justify-content: space-between;
}

.nav-logo{
    position: fixed;
    top:10px;
   left: 0;
   width: 80px;
   object-fit: contain;
   padding-left: 20px;
   cursor: pointer;
}
.nav-avatar{
    position: fixed;
    right: 20px;
    width: 30px;
    cursor: pointer;
}
